                    'info': 'Яйцо «Курочка» открывает прославленную серию из 50 императорских пасхальных яиц, созданную Фаберже для двух последних императоров России между 1885 и 1916 годами. Оно было изготовлено фирмой Фаберже в 1885 году по заказу императора Александра III и предназначалось в подарок супруге – императрице Марии Федоровне, в девичестве датской принцессе Дагмаре. Император не случайно поручил заказ Петеру Карлу Фаберже, изделия фирмы которого очень понравились царской чете во время посещения Всероссийской художественно-промышленной выставки 1882 года в Москве. \n Происхождение курочки как многосоставного сюрприза пасхального яйца относится к началу XVIII века. Известны три очень похожих пасхальных сувенира, предположительно выполненных в Париже в 1720-х годах одним мастером. Это яйцо из сокровищницы Музея истории искусств в Вене, золотое яйцо с эмалевым желтком, до 1924 года находившееся в «Грюнес Гевёльбе» (Зеленом своде) Государственного собрания Дрездена, и яйцо герцогини Вильгельмины, ныне хранящееся в замке Розенборг (Копенгаген). Считается, что последнее, увиденное Александром III и императрицей Марией Федоровной на художественно-промышленной выставке в Копенгагене в 1879 году, должно было послужить источником вдохновения для Фаберже. \n Исполняя императорский заказ, Фаберже создал не копию, а собственную вариацию старинного образца. Мастер отказался от полихромной эмали, алмазных гнезд, резных камней, монограмм и девизов. Благодаря этому яйцо «Курочка» выглядит просто и цельно. Покрывающая его белая опаковая (непрозрачная) эмаль имитирует скорлупу, желток выполнен из матового золота. В него вложена курочка из разноцветного «мозаичного золота», внутри которой – миниатюрная императорская корона. Последний сюрприз, по предложению Александра III, был выполнен в виде рубинового яйца-кулона, к которому прилагалась золотая цепочка (местонахождение короны и кулона неизвестно). \n Курировавший заказ великий князь Владимир Александрович, приходившийся императору родным братом, снабдил готовое изделие письмом с подробной инструкцией. Для раскрытия яйца, во-первых, нужно было «повернуть верхнюю половину налево и снять ее. Во 2х, повторить то-жесамое для желтка. В 3-х, взяв курицу в руки надавить у ней под зобом и одновременно ухватившись ногтями за клюв, приподнять кверху отчего курица раскрывается надвое. В 4х корону следует извлечь основанием наперед; за сим ухватив оную четырьмя перстами за обе половинки, отогнуть их. Таким образом посередине останется бриллиантовый ободок, с висящим на нем вышеупомянутым клейнодом. В 5-х что-бы снять этот клейнод следует нажать ободок у основания, с того конца, где снизу, от базы торчит штифтик. Ободок отгибается и тогда представляется возможность отцепить рубиновое сокровище» (орфография сохранена). \n Император переживал, придется ли сюрприз по вкусу будущей владелице, но яйцо так понравилось, что в том же 1885 году Фаберже стал придворным поставщиком, а высочайший заказ с этого времени возобновлялся в течение 32 лет (1885-1917).'
