                    'year': '1895',
                    'typeOfCanvas': 'Золото, эмаль, бархат, бриллианты, рубины',
                    'info': 'Мария Федоровна, овдовев, по-прежнему получала драгоценные яйца работы Фаберже, теперь уже от сына, продолжившего милую семейную традицию. Для придворного ювелира задача неимоверно усложнилась: отныне он должен был создавать по два совершенно непохожих, но одинаково роскошных яйца – для матери и супруги Николая II, между которыми, по словам современников, не было ни «настоящей близости», ни особого понимания. Столь разные по характеру и вкусам, императрицы наверняка невольно сравнивали пасхальные подарки от «дорогого Ники», за степень влияния на которого боролись с самого начала. \n Яйцо «Бутон розы» (1895) стало первым пасхальным сувениром от Фаберже, врученным принявшей православие Александре Федоровне, в девичестве принцессе Алисе Гессен-Дармштадской. Внучка английской королевы Виктории, она вышла замуж за русского императора в 1894 году, сразу после смерти его отца и траурных мероприятий. Многие расценили это как дурное предзнаменование. Но Николай II был счастлив: наконец он был рядом с милой Аликс, в которую был влюблен с юности. \n Изящное яйцо «Бутон розы» выполнено в неоклассическом вкусе или, как К.Фаберже указывал в счетах, в стиле Людовика XVI. Обращаясь к французскому искусству галантного века, мастера и художники фирмы интерпретировали его в рафинированной слегка суховатой манере конца ХIХ – начала ХХ столетия. Яйцо покрыто ярко-красной (земляничной) прозрачной эмалью по гильошированному фону и украшено символами любви и прочности брачных уз – алмазными стрелами, лиственными гирляндами и венками из зеленоватого золота, перевязанными лентами. Наверху, под большим плоским алмазом, миниатюрный портрет Николая II, написанный акварелью на пластине слоновой кости, а под нижним алмазом начертана дата создания подарка. Сюрпризом является бутон чайной розы (считается, что именно такие цветы больше всего ценились в Гессен-Дармштадтском герцогстве) на коротком стебельке. Внутри бутона – миниатюрная императорская корона (настоящая корона увенчает голову Александры Федоровны год спустя), усыпанная бриллиантами, с двумя рубинами-кабошонами (со временем утраченная). Выполненная ведущим мастером фирмы Михаилом Перхиным роза была одним из первых сюрпризов Фаберже, снабженных механизмом: лепестки открываются при нажатии на маленькую кнопку на стебле.'
