                    'author': 'Мастер: Михаил Перхин',
                    'pictureTitle': 'Пасхальное яйцо «Ландыши»',
                    'year': '1898',
                    'typeOfCanvas': 'Золото, эмаль, алмазы, рубины, жемчуг, горный хрусталь, слоновая кость',
                    'info': 'Яйцо «Ландыши», поднесенное императором Николаем II супруге на Пасху 1898 года, украшено любимыми цветами молодой императрицы Александры Федоровны (символизирующими чистоту, юность и невинность) и столь нравившимися ей жемчугом и бриллиантами. Скорлупа яйца покрыта прозрачной розовой эмалью по гильошированному фону, украшена полосками алмазов и оплетена внизу ландышами на золотых стеблях с зелеными эмалевыми листьями и жемчужными цветками. На лепестках ландышей, подобно утренней росе, сияют алмазы. \n Сюрпризом яйца являются миниатюрные портреты трех самых дорогих Александре Федоровне людей – обожаемого супруга и двух старших дочерей: Ольги (род. 1895) и Татьяны (род. 1897). Медальоны, написанные акварелью на пластинах слоновой кости датским миниатюристом Иоганном Цейнграфом, укреплены в верхней части яйца на металлических «стеблях» по принципу веера. Они появляются изнутри при вращении штифта с жемчужиной. В закрытом состоянии отверстие прикрывает миниатюрная копия императорской короны, усыпанная алмазами (огранки «роза»). \n Современники отмечали, что в императорской семье царила идеальная атмосфера, а самыми счастливыми государь считал часы, которые он проводил в кругу семьи. Пасхальный подарок «Ландыши», выполненный в столь любимом императрицей стиле модерн, лишенном официальности, присущей классическим стилям, как нельзя лучше соответствовал тем глубоким и трогательным чувствам, которые Николай II питал к своей супруге. \n Впрочем, модерн не характерен для «привыкших к известной художественной дисциплине» мастеров прославленной фирмы. В связи с этим интересно вспомнить о реакции на «Ландыши» на ставшей триумфом Нового Искусства (стиля Art Nouveau) Всемирной выставке 1900 г. в Париже, где экспонировались императорские пасхальные шедевры Фаберже. На фоне свободного, не терпящего никаких ограничений и слегка экзальтированного модерна академизм петербургского ювелира, безусловно, выглядел неорганично, но даже самые рьяные защитники нового стиля не могли не восхищаться листьями и цветами яйца «Ландыши», называя их произведением «утонченного вкуса», хотя и досадуя на то, что веточки «слишком близко прилегают к яйцу», ножек не три, а четыре, да и заканчиваются они банальными завитками. \n Несмотря на критику, все выставленные работы Фаберже, отличавшиеся, по заключению экспертной комиссии парижской Выставки, «совершенством исполнения и безукоризненной монтировкой», были распроданы и привлекли ряд заказов и клиентов. Сам же талантливый ювелир, бывший членом жюри и оценивавшийся вне конкурса, был награжден орденом Почетного Легиона и памятной медалью выставки.'
